/* styles.css */

body {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  background-color: #f1f1f1;
  margin: 0;
  font-family: Arial, sans-serif;
}

button {
  font-weight: bold;
}

.app {
  background-color: #fff;
  padding: 20px;
  border-radius: 5px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  width: 1200px;
  height: 100%;
}

.app-title {
  text-align: center;
  margin-bottom: 20px;
  color: #333;
}

.algorithm-selector {
  display: flex;
  justify-content:space-evenly;
  align-items:center;
  text-align: center;
  margin-bottom: 20px;
}

.algorithm-selector button {
  padding: 10px 20px;
  border: none;
  background-color: #007bff;
  color: #fff;
  font-size: 16px;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.algorithm-selector button:hover {
  background-color: #0056b3;
}

.visualization-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 20px;
}

.algo-settings {
  display: flex;
  width: 30%;
  justify-content: space-evenly;
  align-items: center;
}

.array-regenerator {
  margin-bottom: 20px;
}

.regenerate-button {
  padding: 10px 20px;
  border: none;
  background-color: #ff0037;
  color: #fff;
  font-size: 16px;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.regenerate-button:hover {
  background-color: #c40730;
}

.algo-init {
  margin-bottom: 20px;
}

.init-button {
  padding: 10px 20px;
  border: none;
  background-color: #5be738;
  color: #fff;
  font-size: 16px;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.init-button:hover {
  background-color: #56b43f;
}

.visualization {
  display: flex;
  width: 100%;
  height: 500px;
  border: 1px solid #ccc;
  border-radius: 5px;
}

.array-bar {
  background-color: #bdddff;
}

.current {
  background-color: #ff0037;
}

.smallest {
  background-color: #007bff;
}

.sorted {
  background-color: #c87bf8;
}

.pivot {
  background-color: #75ffc1;
}

.low {
  background-color: #f1ffcd;
}

.high {
  background-color: #ffc375;
}